<template>
  <div class="upData">
    <div class="upData-icon">
      <img src="../../../assets/registerIcon/registerBanner.png"/></div>
    <div class="upData-input">
      <van-cell-group>
        <van-field v-model="name" placeholder="请输入姓名" @focus="focusButton"  @input="inputName" :error-message="nameError">
          <img class="nameClass" slot="left-icon" src="../../../assets/registerIcon/name.png" alt="">
        </van-field>
<!--        <van-field v-model="phone" placeholder="请输入手机号" :disabled="disabledPhone" @focus="focusButton">-->
<!--          <img class="nameClass" slot="left-icon" src="../../../assets/registerIcon/phone.png" alt="">-->
<!--        </van-field>-->
<!--        <van-field v-model="code" placeholder="请输入短信验证码" @input="inputButton" :error-message="inputCodeError">-->
<!--          <img class="nameClass" slot="left-icon" src="../../../assets/registerIcon/code.png" alt="">-->
<!--          <div slot="button" class="code" @click="getyzcode"  v-if="showbtn" :disabled="attcode">获取验证码</div>-->
<!--          <div slot="button" class="code" v-else>{{code_ts}}</div>-->
<!--        </van-field>-->
      </van-cell-group>
    </div>
    <div class="upData-tips">
      <div class="tips-title">开户前需做好一下准备：</div>
      <div class="tips-content">
          <div>1、身份证（请确保您的身份证在有限期内）</div>
          <div>2、银行卡（请使用本人借记卡）</div>
      </div>
    </div>
    <div class="upData-but" @click="linkUpId">下一步</div>
  </div>
</template>

<script>
  import { Toast } from 'vant';
import '../../../assets/font/iconfont'
export default {
  name: 'upData',
  data () {
    return {
      // 姓名
      name: '',
      nameError:'',
      // 手机号码
      phone: '',
      // 禁用手机号
      disabledPhone:true,
      // 验证码
      code: '',
      inputCodeError:'',
      // 按钮显示隐藏
      showBut: true,
      attcode: true, // 点击获取验证码按钮判断
      showbtn: true, // 展示获取验证码或倒计时按钮判断
      code_ts: '获取验证码', // 倒计时提示文字
      sec: 60, // 倒计时秒数
      discUrl:'',
      keying:''
    }
  },
  created(){
    this.phone = localStorage.getItem('phone')
    this.discUrl = localStorage.getItem('discUrl')
  },
  watch: {
    // 判断手机号是否输入正确
    phone: function (value) {
      let reg = /\b1[3456789]\d{9}\b/g
      let val = reg.test(value)
      if (val) {
        this.attcode = false
      } else {
        this.attcode = true
      }
    }
  },
  methods: {
    // 验证
    // 验证姓名
    // 验证账号
    inputName(){
      let value = this.name
      let reg = /^[\u4E00-\u9FA5]/g
      let val = reg.test(value)
      if (val) {
        this.nameError = ''
      } else {
        this.nameError = '姓名要求为中文字符请重新输入！'
      }
    },
    // 按钮显示隐藏
    focusButton () {
      this.showBut = false
    },
    blurButton () {
      this.showBut = true
    },
    inputButton () {
      let value = this.code
      let reg = /^\d{5}$/g
      let val = reg.test(value)
      console.log(val)
      if (val) {
        this.inputCodeError = ''
      } else {
        this.inputCodeError = '验证码输入有误，请重新确认！'
      }
    },
    linkUpId () {
      if(this.name != ''){
        this.$router.push({
          name: 'upId',
          params:{
            name:this.name,
            phone:this.phone,
            code:this.code,
          }
        })
      }
      // console.log( bankCardAttribution('6222600260001072444') )
    },
    getyzcode () {
      // 短信发送失败，提示手机号已注册
      this.$axios.get('http://'+this.discUrl+':7777/api/user/sms/' + this.phone).then(res => {
        console.log(res)
        if (res.data.code === 0  && res.data.object != null) {
          Toast({
            duration:1000,
            position:'bottom',
            message:'验证码发送成功'
          })
          this.keying = res.data.object.keying
          localStorage.setItem('openKeying',this.keying)
          let timer = setInterval(() => {
            this.sec = this.sec - 1
            this.code_ts = this.sec + 'S后重试'
            this.showbtn = false
            if (this.sec === 0) {
              clearInterval(timer)
              this.sec = 60
              this.code_ts = this.sec + 'S后重试'
              this.showbtn = true
            }
          }, 1000)
        }else {
          Toast({
            duration:1000,
            position:'bottom',
            message:'验证码发送失败'
          })
        }
      })
    },
  }
}
</script>
<style scoped lang="scss">
  @import "../../../style/register";
</style>



